let initialState = {
    score: 0,
    tasks: [],
    tasksCompleted: [],
    message: 'Add new task!',
    isAddNewOpen: false
}

if(loadFromLocalStorage() !== undefined){
    initialState = loadFromLocalStorage();
}

const reducers = (state = initialState, action) => {

    if(action.type === 'CLEAR'){
        let s = {
            ...state,
            tasksCompleted: []
        }

        console.log('CLEAR', s)
        saveToLocalStorage(s)
        return s;
    }

    if (action.type === 'COMPLETE') {
        let tempArr = [...state.tasks];
        tempArr[action.index].isDone = true;
        let temp = tempArr.splice(action.index, 1);
        let positiveScore = 0;

        if (state.tasks[action.index].priority === 1) {
            positiveScore = 3
        } else if (state.tasks[action.index].priority === 2) {
            positiveScore = 2
        } else if (state.tasks[action.index].priority === 3)
            positiveScore = 1;

        let s = {
            ...state,
            tasks: tempArr,
            score: state.score + positiveScore,
            tasksCompleted: state.tasksCompleted.concat(temp)
        }

        console.log('COMPLETE', s)
        saveToLocalStorage(s)
        return s;
    }

    if (action.type === 'OPEN') {
        let tempArr = [...state.tasks];
        tempArr[action.index].isOpen = !tempArr[action.index].isOpen

        let s = {
            ...state,
            tasks: tempArr
        }
        console.log('OPEN', s)
        saveToLocalStorage(s)
        return s;

    }
    if (action.type === 'DELETE') {


        // Mutating Array
        let tempArr = [...state.tasks];
        tempArr.splice(action.index, 1);

        let negativeScore = 0;
        if (state.score > 0) {
            if (state.tasks[action.index].priority === 1) {
                negativeScore = 3
            } else if (state.tasks[action.index].priority === 2) {
                negativeScore = 2
            } else if (state.tasks[action.index].priority === 3)
                negativeScore = 1;
        }

        //
        let s = {
            ...state,
            score: state.score - negativeScore,
            tasks: tempArr,

        }
        console.log('DELETE', s)
        saveToLocalStorage(s)
        return s;
    }
    
    if (action.type === 'ADD') {
        
        let tempArr = [...state.tasks];
        if(state.isAddNewOpen === true && action.task.priority !== 4 && action.task.priority !== 0 &&  action.task.taskName !== '' &&  action.task.des !== '' ){
            tempArr.push(action.task)
        } 
        
        
        let isOpen = !state.isAddNewOpen;
        
        let s = {
            ...state,
            isAddNewOpen: isOpen,
            tasks: tempArr
            
        }
        
        console.log('ADD', s)
        saveToLocalStorage(s)
        return s;
        

    }        
    return state;
    
}

function saveToLocalStorage(state) {
    try {
        const serializedState = JSON.stringify(state);
        localStorage.setItem('state', serializedState);
    } catch (e) {
        console.log(e)
    }
}

function loadFromLocalStorage(){
    try{
        const serializedState = localStorage.getItem('state');
        if(serializedState === null) return undefined
        return JSON.parse(serializedState)
    } catch(e){
        console.log(e)
        return undefined
    }
}

export default reducers;


