import React from 'react'

import './Tasks.css'
import { Collapse } from 'reactstrap';
import UntickSVG from '../../../Assets/Icons/untick.svg';
import DeleteSVG from '../../../Assets/Icons/delete.svg';

const task = (props) => {

    let fontStyle = null
    if(props.isDone){
        fontStyle={
            textDecoration: 'line-through',
            color: 'grey'
        }
    }


    return (
        <div>
            <div
                style={{
                    borderLeft: `7px solid ${props.priorityColor}`
                }}
                className="task-container" >
                <div style={fontStyle} onClick={props.isOpenHandler} className="task-line">
                    {props.name}
                </div>
                <div className="controls">
            {(props.isDone || props.isOpen) ? null : <button style={{

                backgroundImage: `url(${UntickSVG})`,
                backgroundRepeat: 'no-repeat',
                backgroundPosition: 'center'
                
            }} onClick={props.complete} className="task-button"></button>}
            {(props.isOpen) ? <button style={{

                backgroundImage: `url(${DeleteSVG})`,
                backgroundRepeat: 'no-repeat',
                backgroundPosition: 'center'

            }} onClick={props.deleteTask} className="task-button"></button> : null}
                </div>
            </div>

            <Collapse isOpen={props.isOpen}>
                <div style={{
                    backgroundColor: `#efefef`
                }} className="details">{props.des}</div>
            </Collapse>
        </div>
    )
}

export default task;


