import React from 'react'
import './Heading.css';
import { connect } from 'react-redux';

const heading = (props) => {

    let currColor;
    if (props.score < 5) {
        currColor = '#e57373';
    } else if (props.score < 20) {
        currColor = '#ffb74d';
    } else {
        currColor = '#81c784';
    }

    let scoreStyle = {
        color: currColor
    }


    return (
        <div className="heading">
            <h1 className="title">Todo</h1>
            <h2 style={scoreStyle} className="score">{props.score}</h2>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        score: state.score
    }
}



export default connect(mapStateToProps)(heading);


